





























import { Vue, Component } from 'vue-property-decorator';
import { Error404Data } from '@/types/views/error404.interface';
import BaseImg from '@/components/BaseImg.vue';

@Component({
  components: {
    BaseImg
  }
})
export default class Error404 extends Vue {
  data(): Error404Data {
    return {
      img: require('@/assets/error/error404.png'),
      msg: '哎呀！您访问的页面不存在 (404)',
      msgEng: 'Oops! The page you visited does not exist'
    };
  }
}
